<template>
    <div id="wrapper">
        <navbar/>
        <div class="d-flex flex-column" id="content-wrapper">
            <div id="content">
                <header-btn/>
                <div class="container-fluid">
                    <h3 class="text-dark text-right"> ســتــافـەکـان </h3>
                    <hr>
                    <div class="row" style="overflow-x: auto;">
                        <div class="mb-2 p-1"> 
                            <input type="text" class="form-control text-center" placeholder="Search..." v-model="search">
                        </div>
                        <button class="btn btn-primary mb-2 mx-1" onclick="window.print()"> Print <i class="fa fa-print"></i> </button>
                        <table class="table table-bordered table-striped text-dark text-center" dir="rtl">
                            <thead>
                                <th style="width: 15%;"> کـۆد </th>
                                <th> نـاوی سـتـاف </th>
                                <th> سـەرپـەرشـتـیـار </th>
                            </thead>
                            <tbody>
                                <tr v-for="staff in filtered_staffs" :key="staff.st_id">
                                    <td> {{ staff.st_id }} </td>
                                    <td> {{ staff.staff_name }} </td>
                                    <td> {{ staff.engineer }} </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "staffReport",
        data() {
            return {
                staffs: [],
                search: ""
            }
        },
        created() {
            this.$http.post("/staff/getStaffReport").then(({data}) => {
                this.staffs = data;
            });
        },
        computed: {
            filtered_staffs() {
                return this.staffs.filter(obj => {
                    return obj.st_id == this.search ||
                           obj.staff_name.match(this.search) ||
                           obj.engineer.match(this.search);
                });
            }
        }
    }
</script>

<style scoped>
    @font-face {
        font-family: nrt;
        src: url("../../assets/fonts/NRT-Reg.ttf");
    }
    table, h3 {
        font-family: nrt;
    }
    @media print {
        input, button {
            display: none !important;
        }
    }
</style>