<template>
    <div id="wrapper">
        <navbar/>
        <div class="d-flex flex-column" id="content-wrapper">
            <div id="content">
                <header-btn/>
                <div class="container-fluid">
                    <h3 class="text-dark text-right"> سـەرپـەرشـتـیـارەکان </h3>
                    <hr>
                    <div class="row" style="overflow-x: auto;">
                        <div class="mb-2 p-1"> 
                            <input type="text" class="form-control text-center" placeholder="Search..." v-model="search">
                        </div>
                        <button class="btn btn-success mb-2 mx-1" v-on:click="get_actived()"> Actived </button>
                        <button class="btn btn-danger mb-2 mx-1" v-on:click="get_deactived()"> Deactived </button>
                        <button class="btn btn-primary mb-2 mx-1" onclick="window.print()"> Print <i class="fa fa-print"></i> </button>
                        <button class="btn btn-primary mb-2 mx-1" v-on:click="refresh()"> <i class="fa fa-redo"></i> </button>
                        <table class="table table-bordered table-striped text-dark text-center" dir="rtl">
                            <thead>
                                <th style="width: 8%;"> کـۆد </th>
                                <th> نـاوی یـەکەم </th>
                                <th> نـاوی دووەم </th>
                                <th> کــار </th>
                                <th style="width: 9%;"> ژ.تـەلەفۆن 1 </th>
                                <th style="width: 9%;"> ژ.تـەلەفۆن 2</th>
                                <th style="width: 14%;"> بـەرواری تـۆمارکـردن </th>
                                <th style="width: 7%;"> بـاری ئێستا </th>
                            </thead>
                            <tbody>
                                <tr v-for="engineer in filtered_engineers" :key="engineer.en_id">
                                    <td> {{ engineer.en_id }} </td>
                                    <td> {{ engineer.first_name }} </td>
                                    <td> {{ engineer.last_name }} </td>
                                    <td> {{ engineer.job_title }} </td>
                                    <td> {{ engineer.phone1 }} </td>
                                    <td> {{ engineer.phone2 }} </td>
                                    <td> {{ engineer.reg_date.slice(0, 10) }} </td>
                                    <td v-bind:class="{ 'text-success': engineer.active_status == 1, 'text-danger': engineer.active_status == 0 }"> 
                                        {{ engineer.active_status | status_filter }} 
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "staffReport",
        data() {
            return {
                engineers: [],
                search: ""
            }
        },
        created() {
            this.$http.post("/engineer/getEngineerReport").then(({data}) => {
                this.engineers = data;
            });
        },
        methods: {
            get_actived() {
                this.$http.post("/engineer/getActived").then(({data}) => {
                    this.engineers = data;
                });
            },
            get_deactived() {
                this.$http.post("/engineer/getDeactived").then(({data}) => {
                    this.engineers = data;
                });
            },
            refresh() {
                this.$http.post("/engineer/getEngineerReport").then(({data}) => {
                    this.engineers = data;
                });
            }
        },
        computed: {
            filtered_engineers() {
                return this.engineers.filter(obj => {
                    return obj.en_id == this.search ||
                           obj.first_name.match(this.search) ||
                           obj.last_name.match(this.search) ||
                           obj.job_title.match(this.search) ||
                           obj.phone1.match(this.search) ||
                           obj.phone2.match(this.search) ||
                           obj.reg_date.match(this.search);
                });
            }
        },
        filters: {
            status_filter(value) {
                return ["Deactive", "Active"][value];
            }
        }
    }
</script>

<style scoped>
    @font-face {
        font-family: nrt;
        src: url("../../assets/fonts/NRT-Reg.ttf");
    }
    table, h3 {
        font-family: nrt;
    }
    @media print {
        input, button {
            display: none !important;
        }
    }
</style>